import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import { restartMapper } from '../module/store';
import ButtonApp from '@/components/ButtonApp.vue';
import MessageBox from '@/components/MessageBox.vue';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import MsgBoxTools from '../mixins/MsgBoxTools';
import ListRestart from '../components/ListRestart.vue';
const Mappers = Vue.extend({
    computed: {
        ...restartMapper.mapState(['list']),
        ...restartMapper.mapGetters(['isLoading', 'listSorted'])
    },
    methods: {
        ...restartMapper.mapActions(['editList'])
    }
});
let PageMain = class PageMain extends Mixins(MsgBoxTools, MsgBoxToolsApp, Mappers) {
    constructor() {
        super(...arguments);
        this.editPayload = null; // for repeated request
    }
    get editFailed() { return this.requestStatus === 'failEdit'; }
    // LIST click handler
    onEditClick(payload) {
        if (payload)
            this.editPayload = payload; // stored here in case of repeated request
        this.editList(this.editPayload)
            .then(() => {
            this.editPayload = null;
        })
            .catch(() => {
            this.requestStatus = 'failEdit';
            this.openMsgBox();
        });
    }
    onUpdateClick() {
        this.$emit('updateList');
    }
    // MSGBOX click handler
    onFirstBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failEdit':
                this.onEditClick();
                break;
        }
    }
};
PageMain = __decorate([
    Component({
        components: {
            MessageBox,
            ButtonApp,
            ListRestart
        },
        mixins: [
            MsgBoxTools
        ]
    })
], PageMain);
export default PageMain;
